// Toggle global options

$enable-gradients: false;
$enable-shadows: false;

$grid-columns: 12;
$grid-gutter-width: 2rem;

$btn-padding-y-lg: 0.75rem;
$btn-padding-x-lg: 1.5rem;

$spacer: 1rem;

$gutters: (
    0: 0,
    1: $spacer * 0.25,
    2: $spacer * 0.5,
    3: $spacer,
    4: $spacer * 1.5,
    5: $spacer * 3
);

$offcanvas-box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);

// Customize colors

$body-color: #8989a2;
$body-bg: #fff;

$hero-top-bg: #ffffff;
$hero-bottom-bg: #e2f1ff;

$gradient1-top-bg: rgba(#f9f0ff, 0.47);
$gradient1-bottom-bg: rgba(#e2f1ff, 0.47);

$footer-bg: #fbfcff;
// $border-radius: 0.4rem;

$primary-color: #722ed1;
$sec-color: #7474c0;
$decoration-color: #8989a2;
$apla-color: #f9f0ff;

$stroke-color: #eedff9;
$header-color: #160637;
$footer-color: #160637;
$nav-color: #000000;
$line-color: #d6d6d6;

