//
// Overwrite Boostrap variables
//

$primary: $primary-color; // set the $primary variable
$secondary: $sec-color;

$theme-colors: map-merge(
    $theme-colors,
    (
        "primary": $primary
    )
);

$navbar-dark-color: #000000;
$navbar-dark-hover-color: $primary-color;
$navbar-dark-active-color: rgba($primary-color, 0.8);

$navbar-brand-padding-y: 1.5rem;
$navbar-padding-x: 0;
$navbar-padding-y: 0;
$nav-link-padding-x: 0;
$nav-link-padding-y: 0;
$navbar-nav-link-padding-x: 1.25rem;
$navbar-nav-link-padding-y: 0;

$input-focus-box-shadow: 0 0 0 0.25rem rgb(114 46 209 / 25%);

$font-size-base: 1rem;

$h1-font-size: $font-size-base * 3;
$h2-font-size: $font-size-base * 2;
$h3-font-size: $font-size-base * 1.75;
$h4-font-size: $font-size-base * 1.5;
$h5-font-size: $font-size-base * 1.25;
$h6-font-size: $font-size-base;

$navbar-light-color: #000;
$navbar-light-hover-color: rgb(114 46 209 / 80%);
$navbar-light-active-color: rgb(114 46 209 / 100%);
$navbar-light-icon-color: rgb(114 46 209 / 100%);
$navbar-light-toggler-icon-bg: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'><path stroke='#{$navbar-light-icon-color}' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/></svg>");
$navbar-light-toggler-border-color: rgb(114 46 209 / 25%);
$navbar-toggler-focus-width: 1px;
$dropdown-link-color: #000;

@import "bootstrap";

.caption {
    color: #000000;
}

.btn-primary {
    @include button-variant(
        $primary-color,
        transparent,
        #ffffff,
        darken($primary-color, 15%),
        transparent,
        lighten($primary-color, 30%)
    );
    // border-radius: 99rem;
}

.btn-secondary {
    @include button-variant(
        #ffffff,
        transparent,
        $primary-color,
        darken($primary-color, 15%),
        transparent,
        lighten($primary-color, 30%)
    );
    // border-radius: 99rem;
}
.btn-outline-secondary {
    padding: 0.75rem 1.5rem;
    font-size: 0.875rem;
    @include button-outline-variant($primary-color);
}

.btn-lg {
    padding: 0.75rem 1.5rem;
    font-size: 0.875rem;
}

.btn-google {
    padding: 0;
    border: 1px solid #0069ff;
    background: #0069ff;
    color: #ffffff;
    display: flex;
    display: inline-flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.16);
    font-size: 0.875rem;
    span {
        padding: 0.75rem;
        border-radius: 5px;
        background: #ffffff;
    }
    img {
        width: 20px;
        height: 20px;
    }
    b {
        padding: 0.75rem 0.75rem 0.75rem 0.625rem;
    }
}

//
// Custom styles
//

@media (max-width: 576px) {
    .navbar {
        padding-left: 0.5rem;
        padding-right: 0.5rem;
    }
    .row {
        margin-left: 0;
        margin-right: 0;
    }
}

@import "_fonts";

body {
    margin: 0;
    padding: 0;
    font-family: "Metropolis", sans-serif;
    font-weight: 500;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Metropolis", sans-serif;
    font-weight: 700;
}

section {
    padding: 10rem 0;
    @media (max-width: 576px) {
        padding: 3rem 0;
    }
}

a {
    color: $primary-color;
    text-decoration: underline;
    &:hover,
    &:focus {
        text-decoration: none;
    }
}

:not(.btn-check) + .btn:active {
    border-color: transparent;
}

.form-control {
    &:focus {
        border-color: transparent;
    }
}

.visually-hidden {
    display: none;
}

.btn {
    &-primary {
    }
}

.title {
    color: $header-color;
    &--sub {
        font-size: 1rem;
    }
}

.btn-scroll-to-top {
    position: fixed;
    bottom: 20px;
    right: 20px;
    opacity: 0;
    transition: all 0.3s ease-in;
    z-index: 1000;
    &.show {
        opacity: 1;
    }
    img {
        width: 30px;
        height: 42px;
    }
}

.img-resize {
    max-width: 150%;
    height: auto;
}
.ribbon {
    color: $primary-color;
    margin: 0;
    padding: 0;
    display: inline-block;
    span {
        // border-radius: 99rem;
        display: flex;
        padding: 0.5rem 2.8125rem;
        background: $apla-color;
    }
    &--icon {
        span {
            padding: 0.5rem 1rem 0.5rem 1rem;
        }
        img {
            margin-right: 0.75rem;
        }
    }
}

.logotypes {
    margin: 0;
    padding: 0;
    list-style: none;
    &.grid {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
        grid-gap: 1rem;
        .item {
            padding: 1rem;
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
                filter: contrast(0);
                transition: all 0.3s ease-in;
                &:hover,
                &:focus {
                    filter: none;
                }
            }
        }
    }
    &.list {
        display: flex;
        .item {
            margin: 0.75rem;
            &:first-child {
                margin-left: 0;
            }
        }
    }
}

.header-page {
    background: transparent;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
}

.navbar {
    // padding-right: var(--bs-gutter-x);
    // padding-left: var(--bs-gutter-x);
    .nav-link {
        line-height: 100%;
    }
    .btn-outline-secondary {
        margin-left: 1.25rem;
    }
}

.navbar-collapse.collapsing,
.navbar-collapse.show {
    padding: 0.5rem 0.5rem 1rem 0.5rem;
    background: #f8fcff;

    .navbar-nav {
    }
    .nav-link {
        padding: 0.5rem;
    }
    .dropdown-menu {
        border: none;
        background: transparent;
    }
    .btn-outline-secondary {
        margin: 1rem 0 0 0;
        width: 100%;
    }
}

.hero {
    margin-top: -10rem;
    padding: 10rem 0;
    min-height: 100vh;
    transform: skew(0deg, -5deg);
    background: $hero-bottom-bg;
    background: linear-gradient(0deg, $hero-bottom-bg 0%, $hero-top-bg 100%);
    .container {
        margin-top: 10rem;
        transform: skew(0deg, 5deg);
    }
    .caption {
        margin: 1.5rem 0;
    }
    &-actions {
        margin: 1.5rem 0;
        .btn {
            margin-right: 1.5rem;
            &:last-child {
                margin-right: none;
            }
        }
    }
}

.description-center {
    text-align: center;
}

.icon-bubble {
    margin: 0 auto;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    &.business {
        background: #e6f7ff;
    }
    &.algorithms {
        background: #fff1f0;
    }
    &.data {
        background: #f6ffed;
    }
}

.product-section {
}

.engineering-section {
}

.testimonial-section {
    transform: skew(0deg, -5deg);
    background: $gradient1-bottom-bg;
    background: linear-gradient(0deg, $gradient1-bottom-bg 0%, $gradient1-top-bg 100%);
    .container {
        transform: skew(0deg, 5deg);
    }
    .feature {
        .description {
            margin: 2rem 0;
            font-size: 1.5rem;
            color: $header-color;
        }
        .name {
            margin: 2rem 0;
            font-size: 1rem;
            color: #000000;
            span {
                display: block;
            }
        }
        .avatar {
            margin: 0 auto;
            width: 80px;
            overflow: hidden;
        }
        &-logotype {
            padding: 2rem 1rem;
            border-top: 1px solid $border-color;
            display: flex;
            justify-content: space-between;
            .swiper-slide {
                display: flex;
                align-items: center;
                justify-content: center;
                filter: grayscale(1);
                cursor: pointer;
                &.swiper-slide-thumb-active {
                    filter: none;
                }
            }
        }
    }
}

.singup-section {
    .award {
        margin: 2.1875rem 0 3.125rem 0;
        color: $decoration-color;
        position: relative;
        text-align: center;
        span {
            padding: 0 1.5rem;
            background: #fff;
            z-index: 100;
            position: relative;
        }
        &:before {
            content: "";
            width: 100%;
            height: 1px;
            display: block;
            background: $line-color;
            position: absolute;
            top: 50%;
            left: 0;
            z-index: 90;
        }
    }
    .login {
        background: $apla-color;
        border-radius: 0.4rem;
        text-align: center;
    }
    .login-wrap {
        margin: -5rem 0 0 0;
        padding: 2rem 2.5rem;
        background: #fff;
        border-radius: $border-radius * 2;
        box-shadow: 4px 4px 12px rgba(137, 137, 162, 0);
    }
    .breakout {
        color: #000000;
        display: block;
    }
    .form-control {
        padding: 0.8125rem;
        font-size: 1rem;
    }
    .hint-text {
        margin-bottom: 0;
    }
    .btn-primary {
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    }
}

.why-section,
.product-section,
.engineering-section,
.testimonial-section {
    .title {
        margin: 1.5rem 0;
        &--sub {
            margin: 1rem 0;
        }
    }
    .lead {
        font-size: 1rem;
        line-height: 1.5rem;
    }
}

.footer-page {
    color: $footer-color;
    background: $footer-bg;
    .brand-item {
        margin-bottom: 0.875rem;
    }
    .title {
        margin-bottom: 1.1875rem;
    }
    .newsletter {
        background: #fff;
        box-shadow: 0 5px 9px rgba(0, 0, 0, 0.05);
    }
    a {
        color: $footer-color;
        text-decoration: none;
        &:hover,
        &:focus {
            text-decoration: underline;
        }
    }
}
